#content {
  display: grid;
  justify-items: center;
  text-align: center;
  min-height: 60vh;
}
#content p {
  color: Teal;
  font-weight: bold;
}
#content table {
  border-collapse: collapse;
  border: 1px solid #000;
}
@media screen and (max-width: 630px) {
  #content table {
    font-size: 14px;
  }
}
@media screen and (max-width: 500px) {
  #content table {
    font-size: 10px;
  }
}
@media screen and (max-width: 395px) {
  #content table {
    font-size: 9px;
  }
}
@media screen and (max-width: 330px) {
  #content table {
    font-size: 8px;
  }
}
#content table tr td,
#content table tr th {
  padding: 10px;
  border-left: 1px solid #000;
}
@media screen and (max-width: 630px) {
  #content table tr td,
  #content table tr th {
    padding: 0.5em;
  }
}
