#content {
  display: grid;
  justify-items: center;
  text-align: center;
  min-height: 60vh;
  z-index: -1;
}
#content p {
  color: Teal;
  font-weight: bold;
}
#content /deep/ tr td,
#content /deep/ tr th {
  padding: 10px;
  border-left: 1px solid #000;
}
@media screen and (max-width: 540px) {
  #content /deep/ tr td,
  #content /deep/ tr th {
    font-size: 14px;
    padding: 5px;
  }
}
@media screen and (max-width: 480px) {
  #content /deep/ tr td,
  #content /deep/ tr th {
    font-size: 12px;
  }
}
@media screen and (max-width: 414px) {
  #content /deep/ tr td,
  #content /deep/ tr th {
    font-size: 12px;
  }
}
@media screen and (max-width: 360px) {
  #content /deep/ tr td,
  #content /deep/ tr th {
    font-size: 11px;
  }
}
@media screen and (max-width: 325px) {
  #content /deep/ tr td,
  #content /deep/ tr th {
    padding: 4px;
    font-size: 9px;
  }
}
#charts {
  display: grid;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  grid-template-columns: repeat(2, 48vw);
}
@media screen and (max-width: 768px) {
  #charts {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    grid-template-columns: 92vw;
  }
}
#charts .chart {
  margin-left: 5vw;
  margin-right: 5vw;
}
@media screen and (max-width: 768px) {
  #charts .chart {
    margin-left: 3vw;
    margin-right: 5vw;
  }
}
table {
  background-color: #f00;
}
@media screen and (max-width: 768px) {
  table {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    grid-template-columns: 92vw;
    border-collapse: collapse;
  }
}
