#content {
  display: grid;
  justify-items: center;
  text-align: center;
  min-height: 60vh;
  z-index: -1;

  p {
    color: Teal;
    font-weight: bold;
  }
    & /deep/ tr {
      td, th {
    @media screen and (max-width: 540px){
      font-size: 14px
      padding: 5px
    }
    @media screen and (max-width: 480px){
      font-size: 12px
    }
    @media screen and (max-width: 414px){
      font-size 12px
    }
    @media screen and (max-width: 360px){
      font-size 11px
    }
    @media screen and (max-width: 325px){
      padding: 4px
      font-size 9px
    }
      padding: 10px;
      border-left: 1px solid black;
      }
    }

}
  #charts{
    display grid
    justify-content center
    grid-template-columns repeat(2, 48vw)
    +less-than(tablet) {
      justify-content center
      grid-template-columns 92vw
    }
    .chart {
      +less-than(tablet) {
        margin-left 3vw
        margin-right 5vw

      }
      margin-left 5vw
      margin-right 5vw
    }
  }

  table {
    background-color: red;
    +less-than(tablet) {
      justify-content center
      grid-template-columns 92vw
    border-collapse: collapse;
    }
  }
