










































































































































































































































































































































































































































@import '../../styles/breakpoints'

.title
  display flex
  width 100vw
  justify-content center
  align-items center

.center
  display flex
  width 100vw
  height 90vh
  justify-content center
  align-items center

#info-content
  display grid
  grid-template-columns 45vw 45vw

  grid-template-areas "tank entry"
  width 90vw
  margin auto

  +less-than(tablet)
    grid-template-columns 90vw
    grid-template-areas "tank" "entry" "recipe"

  +less-than(mobile)
    grid-template-columns 90vw
    grid-template-areas "tank" "recipe"

#new-batch
  display: grid
  grid-template-columns: auto
  justify-content: center
  margin: auto
  margin-top: 30px

#recipe
  margin-top 30px

#entry
  +less-than(tablet)
    margin auto

#data
  justify-content center
  width 90vw
  margin auto
  margin-top 30px
  #charts
    display grid
    justify-content center
    grid-template-columns repeat(2, 43vw)
    +less-than(tablet)
      grid-template-columns 90vw
    .chart
      margin-left 1vw
      margin-right 1vw
  h2
    text-align center

#entry
  display flex
  flex-direction column
  justify-content center
  align-items center

#tank
  text-align center
  justify-content center
  padding 15px
  table
    text-align left
    margin auto
    margin-top 30px
  h2, h3, h4
    text-align center
  grid-area tank
