

































































































































@import '../../styles/breakpoints'

#content
  display grid
  grid-template-columns 1fr 1fr
  grid-gap 3vw
  width 80%
  margin auto
  align-items flex-start
  overflow-x: hidden;
  align-content center
  +less-than(1400px)
    width 98%
    align-content center
    grid-template-rows auto
    grid-template-columns 1fr
    justify-content center
  +less-than(400px)
    grid-template-rows auto
    grid-template-columns center 80vw


.element
  padding 20px
  text-align center

#brand
  .inline
    input
      width 49%
