#content {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-areas: 'info';
  justify-items: center;
}
@media screen and (min-width: 1280px) {
  #content {
    grid-template-columns: 1fr;
  }
}
@media screen and (max-width: 768px) {
  #content {
    grid-template-columns: 98vw;
    grid-template-areas: 'info';
  }
}
