












































































@import '../styles/breakpoints'

.navbar {
  display flex
  position sticky
  top 0
  background-color #00aaa6
  justify-content center
  z-index 1
}

.option {
  color #ffffff
  text-decoration none
  padding 5px
  margin 5px
  font-size 16pt
  +less-than(tablet){
    font-size 12pt
  }
}

/* onclick color: 267f7d */
.option:hover {
  background-color: #00ccc7;
}
.active {
  background-color: #267f7d;
}
