html,
body {
  font-family: sans-serif;
  font-size: 12pt;
  min-width: 100vw;
  min-height: 100vh;
  padding: 0;
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #00aaa6;
  font-weight: bold;
  margin: 0;
}
h1 {
  font-size: 2em;
  padding: 2vh;
}
h2 {
  font-size: 1.8em;
  padding: 2vh;
}
h3 {
  font-size: 1.4em;
  padding: 2vh;
}
h4 {
  font-size: 1.2em;
  padding: 1vh;
}
a {
  cursor: pointer;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  color: #2c3e50;
}
button {
  background: #00aaa6;
  color: #fff;
  padding: 10px 30px;
  font-size: 1em;
  border: none;
  -webkit-box-shadow: 0px 3px 6px rgba(0,0,0,0.2);
          box-shadow: 0px 3px 6px rgba(0,0,0,0.2);
  border-radius: 15px;
  font-weight: 400;
  min-width: 200px;
  max-width: 300px;
  margin: 10px;
  outline: none;
  cursor: pointer;
  -webkit-transition: -webkit-box-shadow 0.2s;
  transition: -webkit-box-shadow 0.2s;
  transition: box-shadow 0.2s;
  transition: box-shadow 0.2s, -webkit-box-shadow 0.2s;
}
button:hover {
  -webkit-transition: -webkit-box-shadow 0.2s;
  transition: -webkit-box-shadow 0.2s;
  transition: box-shadow 0.2s;
  transition: box-shadow 0.2s, -webkit-box-shadow 0.2s;
  -webkit-box-shadow: none;
          box-shadow: none;
}
button:active {
  background: #007774;
}
.header {
  font-family: sans-serif;
  width: 100vw;
  background: #00aaa6;
  text-align: center;
  -webkit-box-shadow: 0px 3px 6px rgba(0,0,0,0.2);
          box-shadow: 0px 3px 6px rgba(0,0,0,0.2);
}
.header h2,
.header a {
  color: #fff;
  padding: 0;
  line-height: 50px;
}
.header a {
  position: absolute;
  left: 5px;
  color: #fff;
  text-transform: capitalize;
  text-decoration: none;
}
.header h2 {
  font-size: 1.5em;
  margin: 0;
}
input,
.dropdown {
  padding: 10px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 1em;
  display: block;
  width: 100%;
  color: #00aaa6;
  font-weight: bold;
  outline: none;
  border: none;
  border-bottom: 1px solid #ccc;
  background: none;
}
input:focus,
.dropdown:focus {
  border-bottom: 2px solid #00aaa6;
}
select {
  margin: 10px 0;
  width: 100%;
}
.checkbox {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.checkbox input,
.checkbox p {
  width: auto;
  padding: 0 10px;
}
.inputGroup {
  position: relative;
  margin: 10px 0;
}
.inputGroup label {
  font-size: 18px;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  left: 0;
  bottom: 0;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}
.inputGroup input:focus ~ label,
.inputGroup input:valid ~ label {
  bottom: -20px;
  color: #999;
  font-size: 14px;
}
.dropdown {
  min-height: 30px;
}
.inline {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.logo-med {
  height: 150px;
  width: 150px;
}
.column-1,
.column-2 {
  width: 100vw;
  min-height: calc(100vh - 50px);
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.column-1 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.column-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 20px;
  text-align: center;
}
table {
  margin: auto;
}
.grid {
  display: grid;
  grid-gap: 25px 10px;
}
@media screen and (min-width: 480px) {
  .grid {
    grid-template-columns: repeat(12, 1fr);
  }
  .grid .col-1 {
    grid-column: span 12;
  }
  .grid .col-2 {
    grid-column: span 6;
  }
  .grid .col-3 {
    grid-column: span 4;
  }
  .grid .col-4 {
    grid-column: span 3;
  }
  .grid .col-span-8 {
    grid-column: span 8;
  }
}
